import React, { PureComponent } from 'react'

import Title from 'components/layoutold/Title'
import { Columns, Column } from 'components/layoutold/Columns'
import GradientBorder from 'components/layoutold/GradientBorder'
import SubTitle from './SubTitle'

import Button from 'components/elements/Button'

import TextInput from 'components/forms/TextInput'

import { withRouter } from 'react-router-dom'
import { withState } from 'recost'
import { auth } from 'services'
import Alert from 'components/elements/Alert'
import style from './style.module.scss'

class Login extends PureComponent {
  state = {
    identifier    : '',
    password : '',
  }

  login = () => {
    const redirect = ''
    const { identifier, password } = this.state
    auth.login(identifier, password).then(
      () => {
        this.props.history.push( redirect || '/' )
      }
    ).catch(
      () => this.setState({
        error: true,
      })
    )
  }

  loginSSO = () => {
    this.props.history.push('/loginSSO')
  }

  onChange = (name, value) => {
    this.setState({
      error: false,
      [name]: value,
    })
  } 

  forgotPassword = () => {
    this.props.history.push('/login/forgot')
  }

  render() {
    const {
      identifier,
      password,
      error,
    } = this.state

    return (
      <div>
        <Title text='Login' align='center' />
        <Columns className='is-centered'>
          <Column className='is-one-third has-text-centered'>
            {error &&
              <Alert error title="Invalid email or password" />
            }
            <GradientBorder wrapperClassName={style.marginBottom}>
              <SubTitle>JTI Internal User</SubTitle>
              <Button text="Login" onClick={this.loginSSO} />
            </GradientBorder>
            <GradientBorder>
              <SubTitle>External User Login</SubTitle>
              <TextInput
                name='identifier'
                onChange={this.onChange}
                value={identifier}
                label={false}
                placeholder='username / email'
              />
              <TextInput
                name='password'
                onChange={this.onChange}
                value={password}
                type='password'
                label={false}
                placeholder='password'
              />
              <Columns>
                <Column className='has-text-left'>
                  <Button
                    text="Login"
                    onClick={this.login}
                  />
                </Column>
                <Column className='has-text-right'>
                  <Button 
                    text="Forgot password" 
                    onClick={this.forgotPassword} 
                  />
                </Column>
              </Columns>
            </GradientBorder>
          </Column>
        </Columns>
      </div>
    )
  }
}

const mapStateToProps = (state) => ({
  username        : state.username,
  password        : state.password,
})

export default withState(mapStateToProps)(
  withRouter(
    Login
  )
)
